import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-apply-constract',
  templateUrl: './apply-constract.component.html',
  styleUrls: ['./apply-constract.component.css']
})
export class ApplyConstractComponent implements OnInit {
	menuIndex=1;
	changeMenu(index){
		this.menuIndex = index;
	}
	// 作品介绍
	bookIntro='请填写您想对编辑说的话以及申请签约的理由等';
	// 最大字数限制
	maxSize = 300;
	// 当前字数
	nowSize=0;
	// 监听文本输入
	textChanged(){
		console.log("-=-=-=-"+this.bookIntro.length);
		this.nowSize = this.bookIntro.length;
	};
	// 声明一个数组，用于表示联系方式的选项
	contactions = [{value:"Q",display:'QQ'},{value:"E",display:'邮箱'},{value:"M",display:'MSN'}];
	user={
		contact:"Q",
	};
	applyResult={
		name:"《春光无限》",
		applyTime:"2017-08-01 10:01",
		dealTime:"2017-08-02 10:01",
		state:"审核中",
		remarks:"..."
	};
  constructor() { }

  ngOnInit() {
  }

}



