import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-comment-manager',
  templateUrl: './comment-manager.component.html',
  styleUrls: ['./comment-manager.component.css']
})
export class CommentManagerComponent implements OnInit {
	comment=[
		{number:123456,name:"《春风十里不如你》",comments:789},
		{number:456789,name:"《围城》",comments:666}
	];
	// 声明一个数组，用于存储书籍信息
	// 定义删除书籍的方法
	deleteBook(index){
		console.log(index);
		if (confirm("确定要删除此书吗？")) {
			this.comment.splice(index,1);
		}
	}
	
  constructor() { }

  ngOnInit() {
  }

}



